function navshow(navclass){
    var current = document.getElementsByClassName(navclass);
    current[0].childNodes[0].style.color = "#d94f5c";
    var line = document.getElementsByClassName("menu__line");
    line[0].style.opacity = "1";
    var last = document.getElementsByClassName("menu__item--current");
    if (!last[0].classList.contains(navclass)) {
        last[0].childNodes[0].style.color = "white";
    }
}
function navshowclose() {
    var current = document.getElementsByClassName("menu__item--current");
    current[0].childNodes[0].style.color = "white";
    var line = document.getElementsByClassName("menu__line");
    line[0].style.opacity = "0";
}

export {
    navshow,
    navshowclose
}