<!--  -->
<template>
  <div>
    <div class="banner">
      <nav class="navbar navbar-default menu menu--alonso">
        <div class="container-fluid">
          <div class="navbar-header">
            <a class="navbar-brand" @click.prevent style="cursor:Default;">
              <img class="bannericon" alt="Brand" src="../../assets/index/icon-student.png" />
              <span class="bannerspan">Explorer 无人水质监测</span>
            </a>
          </div>
          <ul class="menu__list">
            <li class="menu__item menu__item--current userpage">
              <a @click.prevent="userpage" class="menu__link">首页</a>
            </li>
            <li class="menu__item myboat">
              <a @click.prevent="myboat" class="menu__link">我的小船</a>
            </li>
            <li class="menu__item datapage">
              <a @click.prevent="datapage" class="menu__link">实时数据</a>
            </li>
            <li class="menu__item controlpage">
              <a @click.prevent="controlpage" class="menu__link">小船控制</a>
            </li>
            <li class="menu__line"></li>
            <li class="nav-item dropdown">
              <a
                class="nav-link dropdown-toggle"
                href="#"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >用户名</a>
              <div class="dropdown-menu animated fadeIn" aria-labelledby="navbarDropdown">
                <a class="dropdown-item" @click="usermsg">查看个人信息</a>
                <a class="dropdown-item" @click="newboat">注册新的小船</a>
                <div class="dropdown-divider"></div>
                <a class="dropdown-item" @click="returnindex">注销</a>
              </div>
            </li>
          </ul>
        </div>
      </nav>
    </div>
    <div class="wrapper">
      <transition :name="transitionName">
        <router-view class="Router"></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
import { menuinit } from "../../js/content/menu";
import { navshow, navshowclose } from "../../js/content/navcontrol";
export default {
  data() {
    return { transitionName: "slide-right" };
  },
  watch: {
    $route(to, from) {
      if (to.path == "/userpage/firstpage") {
        this.transitionName = "slide-right";
      }
      if (to.path == "/userpage/myboat") {
        if (from.path == "/userpage/firstpage") {
          this.transitionName = "slide-left";
        } else {
          this.transitionName = "slide-right";
        }
      }
      if (to.path == "/userpage/datapage") {
        if (from.path == "/userpage/controlpage") {
          this.transitionName = "slide-right";
        } else {
          this.transitionName = "slide-left";
        }
      }
      if (to.path == "/userpage/controlpage") {
        this.transitionName = "slide-left";
      }
    }
  },
  components: {},

  computed: {},

  mounted() {
    menuinit(window);
  },

  methods: {
    userpage() {
      navshow("userpage");
      this.$router.push({ path: "/userpage/firstpage" });
    },
    myboat() {
      navshow("myboat");
      this.$router.push({ path: "/userpage/myboat" });
    },
    datapage() {
      navshow("datapage");
      this.$router.push({ path: "/userpage/datapage" });
    },
    controlpage() {
      navshow("controlpage");
      this.$router.push({ path: "/userpage/controlpage" });
    },
    usermsg() {
      navshowclose();
    },
    newboat() {
      navshowclose();
    },
    returnindex() {
      this.$router.push({ path: "/index" });
    }
  },
};
</script>
<style scoped>
@import "../../style/content/menu.css";
@import "../../style/index/login.css";
@import "../../style/index/profile.css";
@import "../../style/index/regist.css";
.Router {
  position: absolute;
  width: 100%;
  transition: all 0.8s ease;
}

.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(100%, 0);
  transform: translate(100%, 0);
}

.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-100%, 0);
  transform: translate(-100% 0);
}
</style>